export default {
  name: 'Item',
  props: {
    order: {
      type: Object
    }
  },
  data () {
    return {
      showList: false
    }
  },
  methods: {
    handleShow () {
      this.showList = true
    },
    handleHide () {
      this.showList = false
    }
  }
}



